import { Component, h, Prop, Element, Method, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-document-select',
  styleUrl: 'orderDocumentSelect.css'
})
export class OrderDocumentSelect {

    @Element() root: HTMLElement;
    @Prop() name: string;
    @Prop() api: string;
    @Prop({mutable: true}) value: string = "0";

    @Prop() chooseSaleDocument: string;
    @Prop() receipt: string;
    @Prop() invoice: string;

    @Prop({reflect: true, mutable: true}) loading: boolean;

    change(value: string) {
        this.value = value;

        const form = new FormData();
        form.append("value", value);

        var headers = new Headers();
        headers.append('pragma', 'no-cache');
        headers.append('cache-control', 'no-cache');

        this.loading = true;

        fetch(this.api, {
            method: 'POST',
            headers: headers,
            body: form,
            credentials: "same-origin"
        }).then(()=>this.loading=false);
    }

    @Method()
    async Validate() {}

    @Method()
    async IsValid() {
        return !this.loading;
    }

    render() {
        return <Host>
            <span>{this.chooseSaleDocument}</span>

            <div class="toggles">
                <ks-order-toggle active={this.value == "0"} text={this.receipt} name={this.name} value="0"
                    changeHandler={(value)=>this.change(value)}
                />
                <ks-order-toggle active={this.value == "1"} text={this.invoice} name={this.name} value="1"
                    changeHandler={(value)=>this.change(value)}
                />

                {this.loading && <ks-loader dark />}
            </div>
        </Host>;
    }
}