ks-order-document-select {
    display: block;
}

ks-order-document-select .toggles {
    display: flex;
    position: relative !important;
    margin-top: 10px !important;
}

ks-order-document-select .toggles > ks-order-toggle {
    width: 50%;
    box-sizing: border-box;
}

ks-order-document-select[loading] .toggles {
    opacity: 0.3;
    pointer-events: none;
}